.TaskTagMenu {
  position: absolute;
  z-index: 1;
  top: 35px;
  right: 140px; }

.TaskTagMenu .rightShift {
  background-color: #113;
  border: 2px solid #888;
  border-radius: 3px;
  position: relative;
  left: 50%; }

.TaskTagMenu input {
  background: #112;
  background: linear-gradient(to bottom, #113 0%, #112 100%);
  border: none;
  border-bottom: 2px solid #888;
  color: #888;
  line-height: 25px;
  padding: 0px 5px; }

.TaskTagMenu input:focus {
  border: none;
  border-bottom: 2px solid #888;
  color: #fff;
  outline: none; }

.TaskTagMenu input::placeholder {
  color: #888; }

.TaskTagMenu input:focus::placeholder {
  color: #ccc; }
