.TaskTag {
  list-style: none;
  display: inline-block;
  height: 30px;
  line-height: 30px;
  margin-top: 5px;
  margin-right: 5px;
  padding: 0px 5px;
  background-color: #000;
  border: 1px solid #fff;
  border-radius: 5px; }

.TaskTag .removeTag {
  display: inline-block;
  line-height: 20px;
  height: 20px;
  width: 20px;
  position: relative;
  top: 4px;
  left: 2px;
  cursor: pointer; }

.TaskTag .removeTag:hover {
  color: #c11; }
